'use strict';

angular.module('pasturabosc')
    .factory('BootSwatchService', function ($http) {
        return {
            get: function() {
                return $http.get('http://bootswatch.com/api/3.json').then(function (response) {
                    return response.data.themes;
                });
            }
        };
    });
'use strict';

angular.module('pasturabosc')
    .directive('jhSwitchTheme', function() {
        /*Directive binds to anchor to update the bootswatch theme selected*/
        return {
            restrict: 'A',
            scope: {
                theme : '=jhSwitchTheme'
            },
            link: function (scope, element) {
                var currentTheme = angular.element("#bootswatch-css").attr('title');
                if(scope.theme.name === currentTheme){
                    element.parent().addClass("active");
                }

                element.on('click',function(){
                    angular.element("#bootswatch-css").attr("href", scope.theme.css);
                    angular.element(".theme-link").removeClass("active");
                    element.parent().addClass("active");
                });
            }
        };
    });
(function() {
  'use strict';

  angular
    .module('pasturabosc')
    .controller('ServicesController', ServicesController);

  /** @ngInject */
  function ServicesController(services, $log) {
    var vm = this;
	vm.servicesThings = [];
	vm.activeServiceId = 0;

    activate();

    function activate() {
		getServicesThings();
	}
	
	function getServicesThings() {
		services.getServices().then(function(data) {
			vm.servicesThings = data;
			if(data) {
				vm.activeServiceId = vm.servicesThings[0].id;
			}
		})
		.catch(function(err) {
			$log.error(err);
		});
	}	
	
	vm.activateService = function(serviceId) {
		vm.activeServiceId = serviceId;
	}	
	
  }
})();

(function() {
  'use strict';

  angular
    .module('pasturabosc')
    .controller('RecipesDetailController', RecipesDetailController);

  /** @ngInject */
  function RecipesDetailController(recipes, $stateParams, $log) {
	var vm = this;
	vm.detail = {title:'titulo', text:'description'};
	vm.recipesId = $stateParams.recipesId;
	
    activate();

    function activate() {
		loadRecipes();
	}	
	
	function loadRecipes() {
		recipes.getRecipesBy($stateParams.recipesId).then(function(data) {
			$log.debug(data);
			vm.detail = data;
		})
		.catch(function(err) {
			$log.error(err);
		});
	}	
	
  }
})();

